import React, { useState } from 'react';
import {Link,NavLink} from 'react-router-dom'
import './NoticeMain.css'
import imgs from "../images/notice/banner_stamp_attendance.png"

const NoticeMain = () => {
    const [nodicedata] = useState([
        {
            id:1,
            text:'     ',
            checked:true,
            date:'2020.08.04',
            new:true,
        },
        {
            id:2,
            text:'  ',
            checked:true,
            date:'2020.03.17',
            new:true,
        },
        {
            id:3,
            text:'  ',
            checked:false,
            date:'2020.09.11',
            new:true,
        },
        {
            id:4,
            text:'    ',
            checked:false,
            date:'2020.07.23',
            new:false,
        },
        {
            id:5,
            text:'    !',
            checked:false,
            date:'2020.05.29',
            new:false,
        },
        {
            id:6,
            text:'   ',
            checked:false,
            date:'2020.07.02',
            new:false,
        },
        {
            id:7,
            text:'   ',
            checked:false,
            date:'2020.07.02',
            new:false,
        },
        {
            id:8,
            text:'    ',
            checked:false,
            date:'2020.05.16',
            new:false,
        }
    ]) 

        const [linkdata]=useState([
            {
                Link:"/Aha",
                text:'',
                id:1,
            },
            {
                Link:"/Aha",
                text:'',
                id:2,
            },
            {
                Link:"/Aha",
                text:'',
                id:3,
            },
            {
                Link:"/Aha",
                text:'',
                id:4,
            },
            {
                Link:"/Aha",
                text:'',
                id:5,
            },
            {
                Link:"/Aha",
                text:'',
                id:6,
            },
            {
                Link:"/Aha",
                text:'',
                id:7,
            },
            {
                Link:"/Aha",
                text:'',
                id:8,
            },
            {
                Link:"/Aha",
                text:'',
                id:9,
            },
            {
                Link:"/Aha",
                text:'',
                id:10,
            },
            {
                Link:"/Aha",
                text:' ',
                id:11,
            },
            {
                Link:"/Aha",
                text:'',
                id:12,
            }
        ])
    return (
        <div className="noticemain">
            <div>
                <ul>
                {linkdata.map(linkdata=>
                    <li key={linkdata.id}>
                        <Link to={linkdata.Link}>{linkdata.text}</Link>
                    </li>
                )}
               
                    <li className="up">
                        <NavLink to="/Aha/notice" activeClassName="active" > </NavLink>
                    </li>
                    <li>
                        <NavLink to="/Aha/new" > </NavLink>
                    </li>
                    <li>
                        <NavLink to="/Aha/token"  >  </NavLink>
                    </li>
                    <li>
                        <NavLink to="/Aha/about"  > </NavLink>
                    </li>
                </ul>
                   <Link to="/Aha"> <img src={imgs} alt=""></img></Link>
            </div>
            <div>
                <div className="notice">
                    <p></p>
                </div>
                {nodicedata.map(nodicedata=>
                    <div key={nodicedata.id} className="list">
                        <div>   
                            {
                            nodicedata.checked===true && <span 
                            style={{
                                background:'orange',width:'50px',height:'25px',
                                borderRadius:'5px',lineHeight:'25px',color:'white',
                                marginRight:'10px'
                                }}>
                                    
                                </span>
                                }
                           <Link style={{textDecoration:'none'}} to="/Aha/notice"><p>{nodicedata.text}</p></Link> 
                        </div>
                        <div>
                            <span style={{color:'gray', fontWeight:600,marginRight:'10px'}}>{nodicedata.date}</span>
                            {nodicedata.new===true && <span style={{color:'#1fc7c1',fontWeight:700 , textTransform:'uppercase' ,fontSize:'0.8rem'}}>new</span>}
                        </div>
                     
               
                    </div>
                )}
            </div>
        </div>
    );
};

export default NoticeMain;