import React, { useState } from 'react';
import './FaqMain.css'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import {Link} from 'react-router-dom'
const FaqMain = () => {
    const[data,setData]=useState([
        {
            id:1,
            text1:'     ?',
            text:'     . hello@a-ha.io   .      .',
            checked:false,
            button:false
        },
        {
            id:2,
            text1:'    ?',
            text:'        . ,          ,          .',
            checked:false,
            button:true,
            buttontext:'    '
        },
        {
            id:3,
            text1:'    ?',
            text:'        .          .   " "       . (,  /     )',
            checked:false,
            button:true,
            buttontext:' '
        },
        {
            id:4,
            text1:' ?',
            text:' , ,          .',
            checked:false,
            button:false
        },
        {
            id:5,
            text1:'   ?',
            text:'       .',
            checked:false,
            button:true,
            buttontext:' '
        },
        {
            id:6,
            text1:'   .',
            text:'hello@a-ha.io         .',
            checked:false,
            button:false
        },
        {
            id:7,
            text1:' .      ?',
            text:'      .        ,                     .,           FAQ  .',
            checked:false,
            button:false
        },
        {
            id:8,
            text1:'   .',
            text:'PC         " "   .',
            checked:false,
            button:false
        }
    ])

    const datas =(id)=>{
        setData(data.map(data=>data.id===id ? {...data,checked:!data.checked}:{...data,checked:false})) 
    }
    

    return (
        <div className="faqMain">
            <h2>FAQ</h2>
       
                <div>
            
                    {data.map(data=>
                    <div>
                        <div onClick={()=>datas(data.id)}>
                            <p style={data.checked === true ? {color:'#6158f2'}:{color:'black'}} >{data.text1}</p>
                            {data.checked === false ? 
                            <ArrowDropDownIcon style={data.checked === true ? {color:'#6158f2',fontSize:'3rem'}:{color:'black',fontSize:'3rem'}} />
                            :<ArrowDropUpIcon style={data.checked === true ? {color:'#6158f2',fontSize:'3rem'}:{color:'black',fontSize:'3rem'}}/>}
                        </div>

             
                    { data.checked ===true && <span>{data.text}</span>}
                            { data.checked ===false || data.button ===true &&<Link to="/Aha/faq"><button>{data.buttontext}</button></Link>}
                    </div>
                    )
                   }
                </div>
         
        </div>
    );
};

export default FaqMain;