import React from "react";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import './MainMenu.css'
import LocalHospitalIcon from '@material-ui/icons/LocalHospital';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import WorkIcon from '@material-ui/icons/Work';
import GradientIcon from '@material-ui/icons/Gradient';
import SignalCellularAltIcon from '@material-ui/icons/SignalCellularAlt';
import AddLocationIcon from '@material-ui/icons/AddLocation';
import AirplanemodeActiveIcon from '@material-ui/icons/AirplanemodeActive';
import AppsIcon from '@material-ui/icons/Apps';
import DvrIcon from '@material-ui/icons/Dvr';
import FavoriteIcon from '@material-ui/icons/Favorite';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import WbIncandescentIcon from '@material-ui/icons/WbIncandescent';
import desktop from '../images/main/desktop.jpg'



const MainMenu = () => {
  return (
    <div className="Menu">
        <div>
            <span></span>
            <p>     </p>
        </div>
      <Grid style={{display:'flex',maxWidth:'1020px',width:'90vw',justifyContent:'center',margin:'0 auto',flexWrap:'wrap'}}>
        <Paper className="item" ><LocalHospitalIcon className="itemicon" /></Paper>
        <Paper className="item" ><VpnKeyIcon className="itemicon" /></Paper>
        <Paper className="item" ><GradientIcon className="itemicon" /></Paper>
        <Paper className="item" ><WorkIcon className="itemicon" /></Paper>
        <Paper className="item" ><SignalCellularAltIcon className="itemicon" /></Paper>
        <Paper className="item" ><AddLocationIcon className="itemicon" /></Paper>
        <Paper className="item" ><AirplanemodeActiveIcon className="itemicon" /></Paper>
        <Paper className="item" ><AppsIcon className="itemicon" /></Paper>
        <Paper className="item" ><DvrIcon className="itemicon" /></Paper>
        <Paper className="item" ><FavoriteIcon className="itemicon" /> </Paper>
        <Paper className="item" ><WbIncandescentIcon className="itemicon" /></Paper>
        <Paper className="item" ><MonetizationOnIcon className="itemicon" /> </Paper>
      </Grid>
      <img src={desktop} alt="For Men "></img>
    </div>
  );
};

export default MainMenu;
