import React from 'react';
import ahalogo from '../images/main/aha_logo_449d047.png';
import './Footer.css'
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import {Link} from 'react-router-dom'

const Footer = () => {

    return (
   
        <div className="footer">
            <div>
                <div>
                    <img src={ahalogo} alt=""></img>
                    <div className="ahainfo">
                        <ul>
                            <li><Link to="/Aha/about" style={{color:' rgb(170,170,170)',textDecoration:'none'}}> </Link> </li>
                            <li><Link  to="/Aha/faq" style={{color:' rgb(170,170,170)',textDecoration:'none'}}>FAQ</Link> </li>
                            <li><Link to="/Aha" style={{color:' rgb(170,170,170)',textDecoration:'none'}}></Link> </li>
                            <li><Link to="/Aha" style={{color:' rgb(170,170,170)',textDecoration:'none'}}></Link> </li>
                            <li><Link to="/Aha" style={{color:' rgb(170,170,170)',textDecoration:'none'}}></Link> </li>

                        </ul>
                        <small>
                            : hello@a-ha.io :  :   : 144-81-25784
                        </small>
                        <small>&copy;  2020 Aha. All rights reserved.</small>
                    </div>
                    <div className="footericon">
                      <Link to="/Aha">  <FacebookIcon/></Link>
                      <Link  to="/Aha">  <TwitterIcon/></Link>
                    </div>
                </div>
            </div>  
        </div>
      
      
    );
};

export default Footer;